// Include routes
import {routes} from './routes/routes';

// Include ui utils
import {utils} from './ui/utils';

const settings = {
	selectors: {
		$siteContainer: $('.site-container'),
		editTrigger: '[data-edit-template]',
		deleteTrigger: '[data-delete-template]',
		editForm: '[data-js-update-template="true"]',
		editFormSubmission: '[data-js-update-template="true"] form'
	},
	init() {
	},
	editTemplates(){
		const self = this;

		self.selectors.$siteContainer.on('click', self.selectors.editTrigger, (e) => {
			const $template = $(e.currentTarget);

			$(self.selectors.editForm).find('[name="templateName"]').val($template.parent().find('.template-name').text());

			if ($template.parent().data('default').toString().toLowerCase() == 'true'){
				$(self.selectors.editForm).find('[name="defaultTemplate"]').prop('checked', true);
			} else {
				$(self.selectors.editForm).find('[name="defaultTemplate"]').prop('checked', false);
			}

			utils.showForm($(self.selectors.editForm));

			$(self.selectors.editFormSubmission).attr('action',`/tools/extract-tool/template/'${$template.parent().data('id')}`).data('new-id',$template.parent().data('id'));
		});

		self.selectors.$siteContainer.on('submit', self.selectors.editFormSubmission, (e) => {
			const $form = $(e.currentTarget);

			routes.put(
				$form.attr('action'),
				$form.serialize(),
				() => {
					const $newTemplate = $(`[data-id="${$form.data('new-id')}"]`);

					utils.closeForm($(self.selectors.editForm));
					$newTemplate.find('span').text($(self.selectors.editForm).find('[name="templateName"]').val());
					$newTemplate.attr('data-default',$(self.selectors.editForm).find('[name="defaultTemplate"]').prop('checked'));
				}
			);

			return false;
		});

		self.selectors.$siteContainer.on('click', '[data-js-close="true"]', () => {
			utils.closeForm($(self.selectors.editForm));
		});
	}
};

export { settings };



