// Include routes
import {routes} from './../../routes/routes';

// Include ui utils
import {utils} from './../../ui/utils';
import {loaders} from './../../ui/loaders';

// Include modules
import {datalist} from './../../datalist';

// Include templates
import {templates} from './../../../templates/templates';

// Include plugins/libraries
import Url from './../../../../vendor/domurl/url.min';

const associations = {
	selectors: {
		$siteContainer: $('.site-container'),
		campaignSegmentRecord: '[data-campaign-segment="true"]',
		addSegmentRecord: '[data-js-add-segment="true"]',
		updateSegmentRecord: '[data-js-update-segment="true"]',
		buttonAddSegment: '[data-add-segment="true"]',
		confirmationPopup: '.confirmation-popup',
		dataSaveSegment: '[data-save-segment="true"]',
		datalistViewport: '.datalist-viewport'
	},
	init() {
		const self = this;

		self.createCampaignRecords();
		self.editCampaignRecords();
		self.toggleForms();
		self.loadCampaignOnSave();
	},
	createCampaignRecords() {
		const self = this;

		self.selectors.$siteContainer.on('submit', self.selectors.addSegmentRecord, (e) => {
			const $form = $(e.currentTarget);
			const modelType = $form.parent().data('content');
			let mainCampaignId = localStorage.getItem('activeRecordId');

			routes.post(
				$form.attr('action'),
				$form.serialize(),
				() => {
					// Emulate click on the cancel button to refresh view
					$form.find('.btn-secondary').click();
					datalist.campaignSegments(mainCampaignId, modelType);
				}
			);

			return false;
		});
	},
	editCampaignRecords() {
		const self = this;

		// Click on record to edit
		self.selectors.$siteContainer.on('click', self.selectors.campaignSegmentRecord, (e) => {
			const $record = $(e.currentTarget);
			const $parent = $record.parents('.tab-content');
			const segmentType = $parent.data('segment-type');
			let mainCampaignId = localStorage.getItem('activeRecordId');

			loaders.addLoader($parent);

			routes.get(
				`/campaign-management/campaigns/${mainCampaignId}/${segmentType}/single/${$record.data('id')}`,
				(data) => {
					$parent.find(self.selectors.buttonAddSegment).hide();
					$parent.find(self.selectors.datalistViewport).hide();
					$parent.find(self.selectors.updateSegmentRecord).remove();

					if (segmentType === 'books') {
						$parent.append($(templates.editBooks(data.data[0], $parent.data('segment-type'), mainCampaignId)));
					} else {
						$parent.append($(templates.editRecordSelectionCriteria(data.data[0], $parent.data('segment-type'), mainCampaignId, data.books)));
					}
				}
			);
		});

		// Click cancel button on add
		self.selectors.$siteContainer.on('click', `${self.selectors.addSegmentRecord} .btn-secondary`, (e) => {
			const $parent = $(e.currentTarget).parents('.tab-content');

			// Click 'close form button'
			$parent.find(self.selectors.buttonAddSegment).click();
		});

		// Click cancel button on update
		self.selectors.$siteContainer.on('click', `${self.selectors.updateSegmentRecord} .btn-secondary`, (e) => {
			const $button = $(e.currentTarget);
			const $parent = $button.parents('.tab-content');

			$parent.find(self.selectors.updateSegmentRecord).remove();
			$parent.find(self.selectors.buttonAddSegment).show();
			$parent.find(self.selectors.datalistViewport).show();
		});

		// When submitting the update form
		self.selectors.$siteContainer.on('submit', self.selectors.updateSegmentRecord, (e) => {
			const $form = $(e.currentTarget);
			const modelType = $form.parent().data('content');

			loaders.addLoader($form);

			routes.put(
				$form.attr('action'),
				$form.serialize(),
				() => {
					let mainCampaignId = $form.parents('.tabs').find('[data-content="campaign"] form').attr('action');
					let campaignIdNew;

					mainCampaignId = mainCampaignId.split("/");
					campaignIdNew = mainCampaignId[mainCampaignId.length - 1];

					// Emulate click on the cancel button to refresh view
					$form.find('.btn-secondary').click();
					datalist.campaignSegments(campaignIdNew, modelType);
				}
			);

			return false;
		});
	},
	toggleForms() {
		const self = this;

		self.selectors.$siteContainer.on('click', '[data-add-segment="true"]', (e) => {
			const $button = $(e.currentTarget);
			const $parentTab = $button.parents('.tab-content');
			const $dataListViewport = $parentTab.find(self.selectors.datalistViewport);
			const $segmentForm = $parentTab.find('.segment-form');
			const $dataJsUpdateSegment = $parentTab.find(self.selectors.updateSegmentRecord);
			let campaignId = localStorage.getItem('activeRecordId');
			const segmentType = $button.parents('li').data('segment-type');

			if (!$button.hasClass('active-form')) {
				$button.html('<i class="material-icons">&#xE5CD;</i> <span>Close form</span>');
				$dataListViewport.hide();
				$segmentForm.show();
				$('[name="campaign_id"]').val(campaignId);

				if (segmentType !== 'books') {
					routes.get(
						`/campaign-management/campaigns/books/list/${campaignId}`,
						(data) => {
							for (let i=0; i < data.books.length; i++) {
								$parentTab.find($('[name="book"]')).append(`<option value="${data.books[i].id}">${data.books[i].format}</option>`);
							}
						}
					);
				}
			} else {
				if (!$dataJsUpdateSegment.is(':visible')) {
					$dataListViewport.show();
				}
				$segmentForm.hide();
				$button.html($button.data('active-html'));
			}

			$dataJsUpdateSegment.hide();
			$button.toggleClass('active-form');

			return false;
		});
	},
	loadCampaignOnSave() {
		const url = new Url;
		const campaignId = url.query.campaign;

		if (campaignId !== undefined) {
			$(`tr[data-id="${campaignId}"]`).click();
		}
	}
};

export { associations };



